import wPicker from "@/components/w-picker/w-picker.vue";
export default {
	components: {
		wPicker
	},
	data() {
		return {
			oldSize:0,
			pageStyle: 1,
			boxOrderNumber: 6,
			numberModelStatus: false,
			mode: "range",
			// defaultVal: [0, 0, 0, 0, 0, 0, 0],
			tabIndex: 0,
			pickerArrayName: '全部',
			pickerIndex: 0,
			tabList: [{
				mode: "selector",
				name: "全部"
			}, {
				mode: "date",
				name: "日期选择"
			}],
			selectList: [{
				label: "全部",
			}, {
				label: "已售后",
			}, {
				label: "未提货",
			}],
			//输入框的值
			searchValue: '',
			//选择时间的值
			dateValue: "",
			selectTime: '选择日期',
			numberList: [],
			page: 1,
			page_size: 5,
			boxOrderInfo: [],
		}
	},
	onLoad(options) {
		this.pageStyle = options.style
		//动态设置标题栏
		uni.setNavigationBarTitle({
			title: options.style == '2' ? '在途中订单' : options.style == '3' ? '售后到库订单' : '未提货订单'
		});
	},
	onShow() {
		this.getNumberBox()
		this.searchSubmit()
	},
	methods: {
		showNumberModel() {
			this.numberModelStatus = true
		},
		hideNumberModel() {
			this.numberModelStatus = false
		},
		//滑动选值
		toggleTab(item) {
			// console.log(item);
			this.mode = item.mode;
			// // this.defaultVal = item.value;
			this.$refs[item.mode].show();
		},
		//最后的赋值 选择全部、已售后、未提货的确认事件
		onConfirm(val) {
			this.pickerArrayName = val.result;
			switch (val.result) {
				case '全部':
					this.pickerIndex = 0
					break;
				case '已售后':
					this.pickerIndex = 1
					break;
				case '未提货':
					this.pickerIndex = 2
					break;
				default:
					break;
			}
			this.page = 1;
			this.page_size = 20;
			this.boxOrderInfo = [];
			this.searchSubmit();
			this.getNumberBox();
		},
		//最后的赋值 选择时间后点击确认事件
		onConfirmTime(val) {
			this.selectTime = val.result;
			this.dateValue = this.selectTime
			this.page = 1;
			this.page_size = 20;
			this.boxOrderInfo = [];
			this.searchSubmit();
			this.getNumberBox();
		},
		searchInput(e) {
			console.log('输入事件')
			this.searchValue = e.detail.value
		},
		searchCifim() {
			console.log('点击确认事件');
		},

		onReachBottom() {
			this.searchSubmit();
			this.getNumberBox();
		},
		goDetail(e) {
			var order_sn = e.currentTarget.dataset.orderSn;
			uni.navigateTo({
				url: './order_details/order_details?orderSn=' + order_sn
			})
		},
		//获取提货点未领取商品列表 list_type:列表类型 默认未提货列表 1 未提货列表 2 在途中列表 3 售后完成列表
		getNumberBox() {
			var _this = this
			var date = new Date(_this.dateValue).getTime();
			var data = {
				key_word: _this.searchValue,
				search_time: date,
				unclaimed_status: Number(_this.pickerIndex),
				list_type: Number(_this.pageStyle)
			}
			_this.$http.getUnclaimedGoodsInfo(data).then(res => {
				let resData = res.data
				_this.numberList = resData.result.boxGoodsInfo
			})

		},
		// 获取提货点未领取的订单列表 list_type:列表类型 默认未提货列表 1 未提货列表 2 在途中列表 3 售后完成列表
		searchSubmit() {
			var _this = this;
			let isold=false
			var date = new Date(_this.dateValue).getTime();
			var data = {
				key_word: _this.searchValue,
				page: _this.page,
				page_size: _this.page_size,
				search_time: date,
				unclaimed_status: Number(_this.pickerIndex),
				list_type: Number(_this.pageStyle)
			}
			_this.$http.getUnclaimedInfo(data).then(res => {
				let resData = res.data
				if(resData.result.boxOrderInfo.length >0){
					
					if(resData.result.boxOrderInfo.length !==_this.page_size){
						_this.oldSize=resData.result.boxOrderInfo.length
						// 保存上次未满当前条数
					}else{
						
					}
					_this.page+=1
					_this.boxOrderInfo = [..._this.boxOrderInfo ,...resData.result.boxOrderInfo]
					_this.boxOrderNumber = resData.result.count
					}else{
						// console.log('没有更多了');
						uni.showToast({
							icon: "none",
							title: '没有更多了',
							duration: 500
						});
					}
				
			})
		},

	}
}
